export async function exportIframeToSinglePagePdf(iframe: HTMLIFrameElement, fileName = 'note.pdf', opts?: { background?: string; scale?: number; maxPx?: number }) {
  const contentDoc = iframe.contentDocument || iframe.contentWindow?.document;
  if (!contentDoc) throw new Error('Unable to access iframe document.');

  try {
    // wait for fonts if available
    // @ts-ignore
    await contentDoc.fonts?.ready?.catch?.(() => {});
  } catch {}

  const html2canvas = (await import('html2canvas')).default;
  const { jsPDF } = await import('jspdf');

  const root = contentDoc.documentElement || contentDoc.body;
  const width = Math.max(root.scrollWidth, root.clientWidth);
  const height = Math.max(root.scrollHeight, root.clientHeight);

  const scale = opts?.scale ?? Math.min(3, (window.devicePixelRatio || 1) * 2);
  const backgroundColor = opts?.background ?? '#ffffff';

  const canvas = await html2canvas(root as HTMLElement, {
    backgroundColor,
    scale,
    useCORS: true,
    allowTaint: true,
    windowWidth: width,
    windowHeight: height,
    width,
    height,
    document: contentDoc,
    logging: false,
  });

  const maxPx = opts?.maxPx ?? 14400; // safety cap
  let pdfW = canvas.width;
  let pdfH = canvas.height;
  const limiter = Math.max(pdfW, pdfH);
  if (limiter > maxPx) {
    const s = maxPx / limiter;
    pdfW = Math.floor(pdfW * s);
    pdfH = Math.floor(pdfH * s);
  }

  const imgData = canvas.toDataURL('image/jpeg', 0.95);
  const orientation = pdfW >= pdfH ? 'l' : 'p';
  const doc = new jsPDF({ orientation, unit: 'px', format: [pdfW, pdfH], compress: true });
  doc.addImage(imgData, 'JPEG', 0, 0, pdfW, pdfH, undefined, 'FAST');
  doc.save(fileName);
}
